import React, { useCallback } from 'react';
import { useState } from 'react';
import { useDropzone } from 'react-dropzone';
import '../styles/App.css';
import swal from 'sweetalert';

const Dropzone = (props) => {

    const [filesPreview, setfilesPreview] = useState([]);
    const [filesToBeSent, setfilesToBeSent] = useState([]);
    const { handleFileReady } = props;

    const onDrop = useCallback(acceptedFiles => {
        if (acceptedFiles.length) {
            if (filesToBeSent.length) {
                swal('Great!', 'Your file has been replaced!', 'success');
                filesToBeSent.shift();
            }
            setfilesPreview({
                filesPreview,
            });
            setfilesToBeSent({
                filesToBeSent,
            });


            filesToBeSent.push(acceptedFiles);

            for (let i in filesToBeSent) {
                filesPreview.push(
                    <div aria-label={ filesToBeSent[i][0].name }>
                        {filesToBeSent[i][0].name}
                    </div>
                );
            }
            setfilesPreview({
                filesPreview,
            });
            setfilesToBeSent({
                filesToBeSent,
            });

            // Say to parent component that the file is ready
            handleFileReady(filesToBeSent);

        }
    }, [filesPreview, filesToBeSent, handleFileReady]);

    const { getRootProps, getInputProps, acceptedFiles } = useDropzone({
        onDrop,
        accept: '.csv',
        minSize: 0,
    });

    return (
        <div className="background_drag_drop">
            <div { ...getRootProps() } className={ acceptedFiles.length > 0 ? 'ondrop_style loaded' : 'ondrop_style' }>
                <input { ...getInputProps() } />
            </div>
            {
                acceptedFiles.length > 0
                    ? (<ul className="labelpurple_ui_errors_form mt-2">
                        {acceptedFiles.length > 0 && acceptedFiles.map(acceptedFile => (
                            <li key={ acceptedFile.name } className="labelFilePurplePreview">
                        File to be sent is: <br/>
                                {acceptedFile.name}
                            </li>
                        ))}
                    </ul>)
                    : (<ul className="labelpurple_ui_errors_form mt-2"
                        style={ { height: '40px' } } >
                    </ul>)
            }
            
        </div>
    );
};


export default Dropzone; 